@import "https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@400;500;600;700&display=swap";
:root {
  --font-family: "Chakra Petch", serif;
  --font-optical-sizing: auto;
  --font-weight: 387;
  --font-style: normal;
  --bg-gradient: linear-gradient(135deg, #0f0f0f, #1c1c1c, #292929, #363636, #434343);
  --color-text: #00ff18;
  --color-light: #fff;
  --color-dark: #0f0f0f;
}

* {
  box-sizing: border-box;
  color: var(--color-text);
  margin: 0;
  padding: 0;
}

body {
  width: 100vw;
  height: 100vh;
  font-family: var(--font-family);
  font-optical-sizing: var(--font-optical-sizing);
  font-weight: var(--font-weight);
  font-style: var(--font-style);
  background: var(--bg-gradient);
  color: var(--color-light);
  background-repeat: no-repeat;
}

main {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 100%;
  height: 100%;
  display: flex;
}

i {
  color: inherit;
  background-color: inherit;
}

main > img {
  border: 5px solid var(--color-text);
  border-radius: 50%;
  width: 100%;
  max-width: 150px;
  height: auto;
}

canvas {
  pointer-events: none;
  background-color: #0000;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

ul {
  flex-direction: column;
  gap: 1rem;
  list-style: none;
  display: flex;
}

a {
  cursor: pointer;
}

p {
  margin-top: .25rem;
}

h1 {
  font-size: 2rem;
  font-weight: 700;
}

h2 {
  font-size: 1rem;
  font-weight: 400;
}

.social-link {
  border: 1px solid var(--color-text);
  color: var(--color-text);
  border-radius: .5rem;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  padding: .5rem;
  text-decoration: none;
  transition: all .25s;
  display: flex;
}

.social-link:hover {
  color: #000;
  background-color: var(--color-text);
  border-radius: .5rem;
  transform: scale(1.05);
}

.fade-in {
  animation: 1s ease-in-out fadeIn;
}

h2#typewriter {
  text-align: justify;
  max-width: 300px;
  height: 224px;
  font-weight: 400;
  line-height: 1rem;
}

button#audio-switch {
  color: var(--color-text);
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 100%;
  outline: none;
  padding: .5rem;
  font-size: 2rem;
  position: fixed;
  top: 1rem;
  right: 1rem;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes pulse {
  0%, 20%, 40%, 60%, 80%, 100% {
    transform: scale(1);
  }

  10%, 30%, 50%, 70%, 90% {
    transform: scale(1.1);
  }
}

.heart-pulse {
  animation: 4s infinite pulse;
}
